<template>
  <div id="show-menu">
    <div class="menu-cont">
      <div class="save-resume" @click="onShowSaveBar">
        <i class="icon-archive icon-5x"></i>
        <p class="save-des">存储简历</p>
      </div>
      <transition name="fade">
        <div class="save-bar" v-show="showSaveBar">
          <div>
            <label>简历链接：</label>
            <input type="text" v-model="url" placeholder="请输入简历链接存储"/>
          </div>
          <div>
            <label>手机号：</label>
            <input type="number" v-model="tel" placeholder="请输入手机号"/>
          </div>
          <div class="action">
            <label @click="onSaveResume">保存</label>
            <label @click="onShowSaveBar">关闭</label>
          </div>
        </div>
      </transition>
      <div class="search" @click="onShowSearchBar">
        <i class="icon-search icon-5x"></i>
        <p class="search-des">查找简历</p>
      </div>
      <transition name="fade">
        <div class="search-bar" v-show="showSearchBar">
          <div>
            <label>手机号：</label>
            <input type="text" v-model="q" placeholder="请输入手机号搜索"/>
          </div>
          <div class="action">
            <label @click="onSearchClick">搜索</label>
            <label @click="onShowSearchBar">关闭</label>
          </div>
        </div>
      </transition>
      <div class="file" @click="fileClick">
        <i class=" icon-file-alt icon-5x"></i>
        <p class="file-des">生成简历</p>
      </div>
      <div class="download-file" @click="downloadClick">
        <i class=" icon-download-alt icon-5x"></i>
      </div>
      <transition name="fade">
        <div class="download-choice" v-show="showDownloadChoice">
          <div class="dpi96 setdpi">
            <p>96像素/英寸</p>
            <p>A4 794×1123</p>
            <input type="radio" @click="choiceClick({'width':794,'height':1123})">
          </div>
          <div class="dpi120 setdpi">
            <p>120像素/英寸</p>
            <p>A4 1487×2105</p>
            <input type="radio" @click="choiceClick({'width':1487,'height':2015})">
          </div>
          <div class="custom">
            width :<input type="text" v-model="customPx.width" placeholder="px">
            height:<input type="text" v-model="customPx.height" placeholder="px">
            <span class="ok-choice"
                  @click="choiceClick({'width':parseInt(customPx.width),'height':parseInt(customPx.height)})">down</span>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data() {
      return {
        msg: 'hello vue',
        showDownloadChoice: false,
        q: '',
        url: '',
        tel: '',
        customPx: {
          width: "",
          height: ""
        }
      }
    },
    props:{
      showSearchBar: {
        type: Boolean
      },
      showSaveBar: {
        type: Boolean
      }
    },
    methods: {
      fileClick: function () {
        this.$emit('listenShowMenu', {"type": "fileClick", "showFlag": true});
      },
      downloadClick: function () {
        this.showDownloadChoice = !this.showDownloadChoice;
      },
      onShowSearchBar: function () {
        this.$emit('onShowSearchBar')
      },
      choiceClick: function (size) {
        if (size.width && size.height) {
          this.$emit('listenShowMenu', {"type": "choiceClick", "size": size});
          this.showDownloadChoice = !this.showDownloadChoice;
        }
      },
      onSearchClick: function () {
        this.$emit('searchResume', this.q)
      },
      onShowSaveBar: function() {
        this.$emit('onShowSaveBar')
      },
      onSaveResume: function () {
        this.$emit('saveResume', {url: this.url, tel: this.tel})
      }
    },
    components: {}
  }
</script>

<style>
  #show-menu {
    float: left;
    width: 8%;
    height: 100%;
    background: #ffffff;
  }

  .menu-cont {
    position: relative;
    width: 90%;
    height: 99%;
    margin: 0 auto;
    padding-top: 1rem;
    box-sizing: border-box;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .logo, .file, .download-file, .search, .save-resume {
    width: 100%;
    color: #42b983;
    text-align: center;
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }

  .logo a {
    color: #42b983;
  }

  .file .file-des, .search .search-des, .save-resume .save-des {
    font-size: 1.6rem;
  }

  .download-file {
    position: absolute;
    bottom: 2rem;
  }

  .logo, .file, .download-file:hover {
    cursor: pointer;
  }

  .save-bar {
    position: absolute;
    z-index: 9999;
    top: 30px;
    left: 110%;
    width: 50rem;
    display: flex;
    flex-direction: column;
    padding: 10px;
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }
  .save-bar input {
    width: 400px;
    height: 40px;
    border: solid 1px #f2f2f2;
    border-radius: 5px;
    padding: 3px;
  }
  .save-bar > div {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  .save-bar > div > label {
    width: 60px;
  }
  .action > label {
    display: inline-block;
    width: 60px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border: solid 1px #f2f2f2;
    border-radius: 10px;
    color: #ffffff;
  }
  .action > label:first-child {
    background: #3ca777;
    margin-left: 60px;
    margin-right: 5px;
  }
  .action > label:last-child {
    color: #313131;
  }
  .search-bar {
    position: absolute;
    z-index: 9999;
    top: 100px;
    left: 110%;
    width: 50rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }
  .search-bar > div > label {
    width: 60px;
  }
  .search-bar > div > input {
    width: 400px;
    height: 40px;
    border: solid 1px #f2f2f2;
    border-radius: 5px;
    padding: 3px;
    margin-bottom: 8px;
  }

  .download-choice {
    position: absolute;
    z-index: 9999;
    bottom: 3.5rem;
    left: 110%;
    width: 35rem;
    text-align: center;
    background: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .setdpi {
    display: inline-block;
    padding: 1rem;
    font-size: 1.6rem;
    color: #3ca777;
  }

  .custom {
    display: inline-block;
    width: 10rem;
    font-size: 1.6rem;
    color: #3ca777;
  }

  .custom input {
    width: 5rem;
    text-align: right;
  }

  .ok-choice {
    display: inline-block;
    text-align: center;
    border: 1px solid #4cd195;
    border-radius: 6px;
    padding: 1px 8px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  }

  .ok-choice:hover {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0);
    cursor: pointer;
  }
</style>



